<template>
  <div id="canvasContainer"></div>
</template>

<script>
import * as THREE from "three";
// import { LineBasicMaterial } from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer.js";
import { OutlinePass } from "three/examples/jsm/postprocessing/OutlinePass.js";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass.js";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass.js";
import { FXAAShader } from "three/examples/jsm/shaders/FXAAShader.js";
import { GammaCorrectionShader } from "three/examples/jsm/shaders/GammaCorrectionShader";
import { RectAreaLightHelper } from "three/examples/jsm/helpers/RectAreaLightHelper.js";
import { RectAreaLightUniformsLib } from "three/examples/jsm/lights/RectAreaLightUniformsLib.js";

export default {
  name: "ThreeCanvas",
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      ambientLight: null,
      directionalLight: null,
      spotLight: null,
      controls: null,
      raycaster: null,
      mouse: new THREE.Vector2(),
      intersected: null,
      composer: null,
      outlinePass: null,
      renderPass: null,
      effectFXAA: null,
      gammaCorrection: null,
    };
  },
  methods: {
    init: function () {
      this.width = window.innerWidth;
      this.height = window.innerHeight;

      let canvas = document.getElementById("canvasContainer");

      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0x121212);

      const axesHelper = new THREE.AxesHelper(5);
      this.scene.add(axesHelper);

      this.camera = new THREE.PerspectiveCamera(
        50,
        this.width / this.height,
        0.1,
        1000
      ); //this is similar to the perspective matrix in opengl
      this.camera.rotation.y = (30 / 180) * Math.PI;
      this.camera.position.z = 0;
      this.camera.position.y = 10;
      this.camera.position.x = 10;

      const loadingManager = new THREE.LoadingManager(() => {
        const loadingScreen = document.getElementById("loadingScreen");
        loadingScreen.classList.add("fade-out");

        loadingScreen.addEventListener(
          "transitionend",
          this.onLoadingTransitionEnd
        );
      });

      this.renderer = new THREE.WebGLRenderer({
        antialias: true,
      });
      this.renderer.physicallyCorrectLights = true;
      this.renderer.outputEncoding = THREE.sRGBEncoding;
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.shadowMap.enabled = true;
      this.renderer.setSize(this.width, this.height);
      this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
      this.renderer.shadowMap.width = 1024;
      this.renderer.shadowMap.height = 1024;

      this.raycaster = new THREE.Raycaster();

      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
      this.controls.target.y = 3;

      this.initLights();

      const loader = new GLTFLoader(loadingManager);
      var self = this;
      this.loadModels(self, loader);
      //this.setupRoom();
      this.postProcessing();

      this.controls.update();

      canvas.appendChild(this.renderer.domElement); //adds the render's DOM element(the element in the HTML tree structure) as the child of the canvas DOM element

      document.addEventListener("mousemove", this.onDocumentMouseMove);

      window.addEventListener("resize", this.onWindowResize);
    },
    setupRoom: function () {
      //Creates the three planes that acts as the walls of the room
      const skyBoxGeometry = new THREE.BoxBufferGeometry(100, 100, 100);
      const skyBoxMaterial = new THREE.MeshBasicMaterial({ color: 0x87ceeb });
      const skyBox = THREE.Mesh(skyBoxGeometry, skyBoxMaterial);

      this.scene.add(skyBox);
    },
    initLights: function () {
      this.ambientLight = new THREE.AmbientLight(0xffffff, 1);
      this.scene.add(this.ambientLight);

      RectAreaLightUniformsLib.init();

      const rectLight1 = new THREE.RectAreaLight(0x707070, 1, 11, 8.2);
      rectLight1.position.set(3.3, 8.5, -20.5);
      rectLight1.rotation.y = Math.PI;
      // rectLight1.shadow.camera.far = 1000;

      this.scene.add(rectLight1);
      this.scene.add(new RectAreaLightHelper(rectLight1));

      // this.directionalLight = new THREE.DirectionalLight(0xffffff, 100);
      // this.directionalLight.position.set(0, 10, -30);
      // this.directionalLight.castShadow = true;
      // this.directionalLight.target.position.set(0, 0, 0);
      // this.directionalLight.shadow.camera.far = 1000;

      // this.directionalLight.shadow.mapSize.width = this.directionalLight.shadow.mapSize.height = 4096;

      // this.directionalLight.shadow.bias = 0;
      // const d = 10;
      // //setting up the frustrum of the light
      // this.directionalLight.shadow.camera.left = -d;
      // this.directionalLight.shadow.camera.right = d;
      // this.directionalLight.shadow.camera.top = d;
      // this.directionalLight.shadow.camera.bottom = -d;

      this.spotLight = new THREE.SpotLight(0x707070, 80);
      this.spotLight.position.set(0, 10, -40);
      this.spotLight.castShadow = true;
      this.spotLight.target.position.set(0, 0, 0);
      this.spotLight.shadow.camera.far = 1000;

      this.spotLight.shadow.mapSize.width = this.spotLight.shadow.mapSize.height = 4096;

      const d = 10;
      //setting up the frustrum of the light
      this.spotLight.shadow.camera.left = -d;
      this.spotLight.shadow.camera.right = d;
      this.spotLight.shadow.camera.top = d;
      this.spotLight.shadow.camera.bottom = -d;
      this.scene.add(this.spotLight);
    },
    loadModels: function (self, loader) {
      loader.load(
        "./room.gltf",
        function (gltf) {
          const scene = gltf.scene;
          scene.traverse(function (child) {
            if (child.isMesh) {
              child.castShadow = true;
              child.receiveShadow = true;
              child.material.side = THREE.DoubleSide;
              child.material.shadowSide = THREE.FrontSide;
            }
          });
          // scene.side = THREE.BackSide;

          // console.log(scene);

          gltf.scene.position.x = 17;
          gltf.scene.position.y = 5.2;
          gltf.scene.position.z = -6.5;

          self.scene.add(gltf.scene);
        },
        undefined,
        function (error) {
          console.log("something went wrong");
          console.error(error);
        }
      );
      loader.load(
        "./desk.gltf",
        function (gltf) {
          const scene = gltf.scene;
          scene.traverse(function (child) {
            if (child.isMesh) {
              child.castShadow = true;
              child.receiveShadow = true;
              child.material.side = THREE.FrontSide;
            }
          });
          // scene.side = THREE.BackSide;

          // console.log(scene);

          const box = new THREE.Box3().setFromObject(gltf.scene); //centers the object at the origin
          const center = box.getCenter(new THREE.Vector3());
          gltf.scene.position.x += gltf.scene.position.x - center.x;
          gltf.scene.position.y += gltf.scene.position.y - center.y + 0.648;
          gltf.scene.position.z += gltf.scene.position.z - center.z;

          self.scene.add(gltf.scene);
        },
        undefined,
        function (error) {
          console.log("something went wrong");
          console.error(error);
        }
      );
      loader.load(
        "./monitor.gltf",
        function (gltf) {
          const scene = gltf.scene;
          scene.name = "Monitor";
          scene.traverse(function (child) {
            if (child.isMesh) {
              child.castShadow = true;
              child.receiveShadow = true;
              child.material.side = THREE.FrontSide;
            }
          });

          // const box = new THREE.Box3().setFromObject(gltf.scene); //centers the object at the origin
          // const center = box.getCenter(new THREE.Vector3());
          gltf.scene.position.x += -1;
          gltf.scene.position.y += 2.95;
          gltf.scene.position.z += 4;

          gltf.scene.rotation.y = (120 / 180) * Math.PI;

          self.scene.add(gltf.scene);
        },
        undefined,
        function (error) {
          console.log("something went wrong");
          console.error(error);
        }
      );
      loader.load(
        "./book.gltf",
        function (gltf) {
          const scene = gltf.scene;
          scene.traverse(function (child) {
            if (child.isMesh) {
              child.castShadow = true;
              child.receiveShadow = true;
              child.material.side = THREE.FrontSide;
            }
          });

          gltf.scene.position.x += 1;
          gltf.scene.position.y += 2.85;
          gltf.scene.position.z += -3;
          gltf.scene.rotation.y = (-20 / 180) * Math.PI;

          self.scene.add(gltf.scene);
        },
        undefined,
        function (error) {
          console.log("something went wrong");
          console.error(error);
        }
      );
    },
    postProcessing: function () {
      this.composer = new EffectComposer(this.renderer);

      this.renderPass = new RenderPass(this.scene, this.camera);
      this.outlinePass = new OutlinePass(
        new THREE.Vector2(this.width, this.height),
        this.scene,
        this.camera
      );

      this.effectFXAA = new ShaderPass(FXAAShader);
      this.effectFXAA.uniforms["resolution"].value.set(
        1 / window.innerWidth,
        1 / window.innerHeight
      );
      this.gammaCorrection = new ShaderPass(GammaCorrectionShader);
      this.composer.addPass(this.renderPass);
      this.composer.addPass(this.outlinePass);
      this.composer.addPass(this.effectFXAA);
      this.composer.addPass(this.gammaCorrection);
    },
    animate: function () {
      requestAnimationFrame(this.animate);
      if (
        this.intersected &&
        this.intersected.parent.name == "Monitor" &&
        this.intersected.parent.type == "Group"
      )
        this.controls.update();

      // this.render();
      this.composer.render();
    },
    onDocumentMouseMove: function (event) {
      //console.log("mouse moved");

      event.preventDefault();

      this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
      this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
      this.checkIntersection();
    },
    onWindowResize: function () {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();
      this.effectFXAA.uniforms["resolution"].value.set(
        1 / window.innerWidth,
        1 / window.innerHeight
      );
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      this.composer.setSize(window.innerWidth, window.innerHeight);
    },
    checkIntersection: function () {
      this.raycaster.setFromCamera(this.mouse, this.camera);
      const intersects = this.raycaster.intersectObjects(
        this.scene.children,
        true
      );

      if (
        intersects.length > 0 &&
        intersects[0].object.parent.name == "Monitor"
      ) {
        if (this.intersected != intersects[0].object) {
          if (this.intersected) {
            this.outlinePass.selectedObjects = [];
          }

          this.intersected = intersects[0].object.parent;
          this.outlinePass.selectedObjects = [this.intersected];
        }
      } else {
        if (this.intersected) this.outlinePass.selectedObjects = [];
        this.intersected = null;
      }

      //this.renderer.render(this.scene, this.camera);
    },
    onLoadingTransitionEnd: function (event) {
      event.target.remove();
    },
  },
  mounted() {
    this.init();
    this.animate();
  },
};
</script>

<style scoped>
#canvasContainer {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
/* h1 {
  position: relative;
  z-index: 1;
} */
</style>